@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page {
  width: 550px;
  background-color: #333f6b;
  height: 385px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  display: flex;
  align-items: center;
}

.page-border-radius {
  border-radius: 15px;
}

.page--container {
  width: 90%;
  height: 330px;
  margin: 0px auto;
  background-color: aliceblue;
}

.button--button {
  width: 125px;
  height: 55px;
  background-color: #132152;
  color: #ffffff;
  display: block;
  margin-left: auto;
  margin-right: 50px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: 0.3s;
}

.button--button:hover {
  background-color: #434f7a;
}

.attendees--container {
  position: relative;
  margin-left: 30px;
  margin-top: 25px;
}

.attendee--image {
  position: absolute;
}

.attendees-text {
  position: absolute;
  left: 135px;
}

.info--container {
  margin-left: 30px;
  width: 75%;
  font-family: "Poppins", sans-serif;
}

.info--navy-blue {
  color: #434f7a;
}

.info--dark-blue {
  color: #132152;
}

.info-align-location {
  display: inline-flex;
  align-items: center;
  margin: 0;
}

.info--date {
  margin-top: 40px;
  margin-bottom: 0px;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.info--title {
  margin: 0;
  font-weight: 600;
}

